<template>
    <div class="box-full">
        <div class="row-header">
            <span class="biger-text-form-m text-color-default">{{label.txt_header_list_bookstop}}</span>
            <span class="medium-text-form-r text-color-highlight display-right">
                <!-- <a href="bookstops" class=""> -->
                <a @click="$emit('showErrorDialog', label.txt_comming_soon)" class="mouse-pointer">{{label.view_all}}
                    <i class="fa fa-angle-right fa-lg" aria-hidden="true"></i>
                </a>
            </span>
        </div>
        <hr class="line-break reset-padding reset-margin">
        <div class="list-bookstop-content">
            <div class="list-bookstop-box-left" v-if="isShowBookstopLeft">
                <router-link :to="{ name: label.NAME_BOOKSTOP_DETAIL_PAGE, params: { id: bookstops[0].id }}">
                    <div class="box-block box-image">
                        <img class="image-fill img-book" alt="">
                    </div>
                </router-link>
                <div class="box-block list-bookstop-left-caption">
                    <p class="medium-text-form-m text-color-default">{{bookstops[0].name.toUpperCase()}}</p>
                    <div class="medium-text-form-r text-color-default list-bookstop-box-left-caption-content">
                        {{getContentLimitLength(bookstops[0].description, maxLengthOfDescript)}}
                        <router-link :to="{ name: label.NAME_BOOKSTOP_DETAIL_PAGE, params: { id: bookstops[0].id }}" v-if="isShowLimitReview" class="medium-text-form-r text-color-highlight">{{label.txt_show_more}}</router-link>
                    </div>
                </div>
            </div>
            <div class="list-bookstop-box-right">
                <div class="list-bookstop-right-caption box-shadow" v-for="(item, index) in bookstops" :key="index" v-on:click="goToBookstopDetail(item)" v-if="index > 0">
                    <div class="list-bookstop-box-avatar">
                        <img class="image-fill img-book" alt="">
                    </div>
                    <div class="list-bookstop-right-box-caption-content">
                        <p class="medium-text-form-m text-color-default">{{item.name.toUpperCase()}}</p>
                        <p class="small-text-form-r text-color-author">{{item.address}}</p>
                        <div class="row reset-margin list-bookstop-right-box-cation-footer">
                            <div class="col-xs-6 reset-padding">
                                <span>
                                    <img src="../../../../assets/images/icon_book.png" class="list-bookstop-right-box-cation-footer-book">
                                </span>
                                <span class="small-text-form-r text-color-author">{{item.sharingCount}}</span>
                            </div>
                            <div class="col-xs-6 reset-padding">
                                <span>
                                    <img src="../../../../assets/images/icon_distance.png" class="list-bookstop-right-box-cation-footer-map-marker">
                                </span>
                                <span class="small-text-form-r text-color-author">{{item.distance}}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>

var obj = {
    id: 0,
    name: "Quc L 1 BookStop",
    address: "S 100 ng Cc, ng a, H Ni",
    description: "Sau  gn mt nm, cu quyt nh ri tim pha l v cu  c  tin mua mt by cu mi, gp i s cu cu bn i hi trc. Nhng cu li khng mua mt con cu no c. Bi v cu quyt nh i tm Truyn thuyt bn thn ca mnh -Hn  Trit Gia trong gic m kia. Cu gia nhp on l hnh n mt sa mc ni gn cc kim t thp. Trong on ngi, cu gp mt ngi Anh  tm kim nhng nh gi kim chn chnh sut 20 nm nay. Ngi Anh ny c hng thng sch v thuyt gi kim khin cu kinh ngc. Cu hc c rt nhiu iu dc ng i. on ngi tin dn v pha c o. Khi ngi Anh n th quan st sa mc v hc ngn ng sa mc, th Edward c nhng cun sch ca anh ta v hc v thut gi kim. Ngi Anh ni vi cu rng mc ch ca nhng nh gi kim l tinh lc kim loi bng cch nung luyn trong nhiu nm. Khi h ti c o, on l hnh c cho n, v c khuyn phi dng bc v c chin tranh gia cc b tc pha trc. Cu  gip ngi Anh tm mt nh gi kim. V  ni , cu gp Fatima, v yu c ngay t ci nhn u tin. Fatima l mt ngi ph n sa mc v c cng  ch i ngi n ng ca i mnh t rt lu ri cho ti khi gp chng. Trong mt ln nh cc cuc i ca mnh vo trc gic v s nhy bn ca mnh, cu  gip b tc trnh c mt cuc tn cng t ngt vo c o. Cu c thng rt nhiu tin vng, cng mt li  ngh tr thnh ngi c vn quan trng cho trng tc.Cng chnh lc y nh gi kim  c o nhn ra rng ng  tm thy mt hc tr xut sc. Hin nhin  chnh l Santiago. Cu lng l khi phi ri c o v tnh yu vi Fatima cng nh cuc sng kh giu sang v danh vng. Nhng nh gi kim  bo trc cu s mt i kh nng nhn thy im bo, ri s mt chc v s hi hn v  khng theo ui gic m ca mnh  i tm kho tng. Sau , vi s khch l ca Fatima, cu  ri c o, ln ng i ti Kim T Thp. Ti mt vng sa mc cai qun bi nhng chin binh hung d, cu phi vt qua nhng ca i, tr ti bin thnh gi, thu hiu ngn ng ca v tr th mi c php i qua. Ti y, nh gi kim t bit chng trai v chng mt mnh i ti chn kim t thp. Cu  n c Kim T Thp, ri li mt ln trng tay khi ng  nhng tn cp. Nhng cu  tm ra v tr t kho bu,  mt ni rt bt ng. Cui cng Santiago  ly c kho bu, tr li c o vi nim vui hn hoan v nim hnh phc tuyt vi.",
    sharingCount: 200, distance: 73239
}
var arr = [];
for (var i = 0; i < 4; i++) {
    arr[i] = obj;
    obj.id++;
}

import BookService from '../../../../services/BookService.js';
import CommonService from '../../../../services/CommonService.js';
import Label from '../../../../common/Label.js';

export default {
    mixins: [CommonService, Label, BookService],
    data() {
        return {
            bookstops: arr,
            isShowLimitReview: false,
            maxLengthOfDescript: 750,
            config: {
                params: {
                    per_page: 5,
                    page: 1
                }
            }
        }
    },
    mounted() {
        this.initData();
    },
    methods: {
        initData() {
            this.getListBookstops();
            if (this.isNotNull(this.bookstops[0]) && this.bookstops[0].description.length > this.maxLengthOfDescript) {
                this.isShowLimitReview = true;
            }
        },
        getListBookstops() {
            this.api.callGetApi(this.api.api_get_bookstop_list, this.config).then(this.setListBookstops, this.errorCallApiListBookstops);
        },
        setListBookstops(response) {
            if (200 == response.status) {
                // this.bookstops = response.data.data.resultInfo;
                this.bookstops = this.bookstops.concat(response.data.data.resultInfo);
            }
        },
        errorCallApiListBookstops(error) {
            this.$emit("showErrorDialog", error);
        },
        goToBookstopDetail(bookstop) {
            this.$router.push({ name: this.label.NAME_BOOKSTOP_DETAIL_PAGE, params: { id: bookstop.id } });
        }
    },
    computed: {
        isShowBookstopLeft() {
            return this.isNotNull(this.bookstops) && this.bookstops.length > 0;
        }
    }
}
</script>


<style lang="scss">
@import 'ListBookStop.scss';
</style>



