<template>
    <div class="book-detail-content-user-comment-form">
        <div class="book-detail-list-user-comment-icon">
            <user-icon :userIconStyle="userIconStyle" :imageId="imageId"></user-icon>
        </div>
        <div class="book-detail-list-user-comment-form-info">
            <textarea id="editComment" class="form-control book-detail-list-user-comment-form-input" :rows="maxRowComment" v-model="comment"></textarea>
            <div class="row book-detail-list-user-comment-form-action">
                <div class="col-xs-6 ">
                    <div class="book-detail-comment-rate-box">
                        <div class="medium-text-form-m book-detail-list-user-comment-txt-rate">
                            {{label.txt_rate_start}}
                        </div>
                        <div class="book-detail-list-user-comment-rate-start ">
                            <star-rating @rating-selected="setRateBook" :rating="rate" :increment="rateIncrement" :show-rating="false" inactive-color="white" border-color="#f4c312" :border-width="1" :star-size="14">
                            </star-rating>
                        </div>
                    </div>
                </div>
                <div class="col-xs-6 reset-padding" v-if="isUpdate">
                    <button class="btn-no-border btn-gat-cancel book-detail-list-user-comment-cancel" @click="$emit('cancel')">
                        {{label.txt_cancel_update_comment}}
                    </button>
                    <button class="btn-no-border book-detail-list-user-comment-create" @click="addComment">
                        {{label.txt_update_comment}}
                    </button>
                </div>
                <div class="col-xs-6 reset-padding" v-else>
                    <button class="btn-no-border book-detail-list-user-comment-create" @click="addComment">
                        {{label.txt_create_comment}}
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>


<script>

import UserIcon from '../../../common/userIcon/UserIcon';
import CommonService from '../../../../services/CommonService.js';
import BookService from '../../../../services/BookService.js';
import StarRating from 'vue-star-rating';

import Label from '../../../../common/Label.js';
import * as CONST from '../../../../common/const.js';
import * as moment from 'moment';

export default {
    name: 'list-user-create-comment',
    components: { UserIcon, StarRating },
    mixins: [CommonService, StarRating, Label],
    data() {
        return {
            userIconStyle: {
                'background-color': 'white',
                'border-radius': '18px',
                'width': '50px',
                'height': '50px',
                'float': 'left',
            },
            idOfCommentInput: 'editComment',
            rate: 0,
            comment: '',
            rateIncrement: 1,
        };
    },
    mounted() {
        if (this.isNotNull(this.selfComment)) {
            this.rate = this.selfComment.value;
            this.comment = this.selfComment.review;
        }
        // If user does not login, can't not rate
        if (!localStorage.getItem(CONST.LOGIN_TOKEN)) {
            this.rateIncrement = 0;
        }
    },
    methods: {
        setRateBook(newRate) {
            // If user login, set rate of book
            if (localStorage.getItem(CONST.LOGIN_TOKEN)) {
                this.rate = newRate;
            }else{
                // Show dialog request login
                this.$emit('showRequestLoginDialog');
            }
        },
        addComment() {
            this.selfComment.value = this.rate;
            this.selfComment.review = this.comment;
            var date = moment();
            this.selfComment.evaluationTime = date.format("DD MMM");
            this.$emit('addComment');
        }
    },
    props: {
        imageId: {
            required: true
        },
        selfComment: {
            required: true
        },
        isUpdate: {
            required: true,
            default: false
        }
    },
    watch: {
        'comment': function() {
            this.typingComment(this.idOfCommentInput);
        }
    }
}
</script>


<style lang="scss">
@import '../../../../scss/common_styles.scss';
@import '../../../../scss/text_styles.scss';
@import 'ListUserCreateComment.scss';
</style>




