<template>
    <div class="user-profile-detail-form box-shadow-bold">
        <!-- navigation bar (include sharingBook, notSharingBook and lostBook) -->
        <ul class="nav nav-tabs tab-header">
            <li class="read-history-na-tab">&nbsp</li>
            <li class="read-history-tab" :class="{'active': $route.query.nav=='read'}">
                <router-link :to="{ name: label.NAME_USER_PROFILE_PAGE, params: {tab: 'read-history'}, query: {nav: 'read', page: 1}}" data-toggle="tab">{{label.readBook}}</router-link>
            </li>
            <li class="read-history-tab" :class="{'active': $route.query.nav=='reading'}">
                <router-link :to="{ name: label.NAME_USER_PROFILE_PAGE, params: {tab: 'read-history'}, query: {nav: 'reading', page: 1}}" data-toggle="tab">{{label.readingBook}}</router-link>
            </li>
            <li class="read-history-tab" :class="{'active': $route.query.nav=='toread'}">
                <router-link :to="{ name: label.NAME_USER_PROFILE_PAGE, params: {tab: 'read-history'}, query: {nav: 'toread', page: 1}}" data-toggle="tab">{{label.toReadBook}}</router-link>
            </li>
            <li class="read-history-na-tab">
                <div>
                <span v-on:click="setViewMode($event, false)" class="fa fa-th-list view-mode active"></span>
                <span v-on:click="setViewMode($event, true)" class="fa fa-th-large view-mode"></span>
                </div>
            </li>
        </ul>

        <div class="tab-content">
            <div id="read-book-form" class="book-in-each tab-pane fade" :class="{'in active': $route.query.nav=='read'}">
                <div v-if="isShowLoading_1" style="margin:10px;">
                    <img src="../../../../assets/images/loading.gif" class="loading-img-medium">
                </div>
                <div v-if="!isShowLoading_1&&readTotal > 0">
                    <div v-if="!gridView" class="row book-list-header medium-text-form-m">
                        <div class="col-xs-5">{{label.bookTitle}}</div>
                        <div class="col-xs-7">{{label.review}}</div>
                    </div>
                    <div v-if="!gridView" class="row" v-for="book in readBookList">
                        <div class="col-xs-12 nopadding">
                            <book-in-read-history-list :book = "book" :refresh="refresh"></book-in-read-history-list>
                        </div>
                    </div>
                    <div v-if="gridView" class="row" v-for="splitedBook in readBooks">
                        <div class="col-xs-3 nopadding" v-for="book in splitedBook">
                            <book-in-read-history :book = "book" :refresh="refresh"></book-in-read-history>
                        </div>
                    </div>
                    <div v-if="readTotal <= per_page" style="height:15px;"></div>
                    <div v-if="readTotal > per_page" class="text-center">
                        <uib-pagination
                            :boundary-links="true"
                            :force-ellipses="false"
                            :boundary-link-numbers="true"
                            previous-text="<" 
                            next-text=">" 
                            first-text="<<"
                            last-text=">>"
                            v-model="readPagination" 
                            :items-per-page="per_page" 
                            :total-items="readTotal" 
                            :max-size="4" 
                            :rotate="true">
                        </uib-pagination>
                    </div>
                </div>
                <div v-if="!isShowLoading_1&&readTotal == 0" style="margin-left:10px;">{{readText}}</div>
            </div>
            <div id="reading-book-form" class="book-in-each tab-pane fade" :class="{'in active': $route.query.nav=='reading'}">
                <div v-if="isShowLoading_2" style="margin:10px;">
                    <img src="../../../../assets/images/loading.gif" class="loading-img-medium">
                </div>
                <div v-if="!isShowLoading_2&&readingTotal > 0">
                    <div v-if="!gridView" class="row book-list-header medium-text-form-m">
                        <div class="col-xs-5">{{label.bookTitle}}</div>
                        <div class="col-xs-7">{{label.review}}</div>
                    </div>
                    <div v-if="!gridView" class="row" v-for="book in readingBookList">
                        <div class="col-xs-12 nopadding">
                            <book-in-read-history-list :book = "book" :refresh="refresh"></book-in-read-history-list>
                        </div>
                    </div>
                    <div v-if="gridView" class="row" v-for="splitedBook in readingBooks">
                        <div class="col-xs-3 nopadding" v-for="book in splitedBook">
                            <book-in-read-history :book = "book" :refresh="refresh"></book-in-read-history>
                        </div>
                    </div>
                    <div v-if="readingTotal <= per_page" style="height:15px;"></div>
                    <div v-if="readingTotal > per_page" class="text-center">
                        <uib-pagination
                            :boundary-links="true"
                            :force-ellipses="false"
                            :boundary-link-numbers="true"
                            previous-text="<" 
                            next-text=">" 
                            first-text="<<"
                            last-text=">>"
                            v-model="readingPagination" 
                            :items-per-page="per_page" 
                            :total-items="readingTotal" 
                            :max-size="4" 
                            :rotate="true">
                        </uib-pagination>
                    </div>
                </div>
                <div v-if="!isShowLoading_2&&readingTotal == 0" style="margin-left:10px;">{{readingText}}</div>
            </div>
            <div id="to-read-book-form" class="book-in-each tab-pane fade" :class="{'in active': $route.query.nav=='toread'}">
                <div v-if="isShowLoading_3" style="margin:10px;">
                    <img src="../../../../assets/images/loading.gif" class="loading-img-medium">
                </div>
                <div v-if="!isShowLoading_3&&toReadTotal > 0">
                    <div v-if="!gridView" class="row book-list-header medium-text-form-m">
                        <div class="col-xs-5">{{label.bookTitle}}</div>
                        <div class="col-xs-7">{{label.review}}</div>
                    </div>
                    <div v-if="!gridView" class="row" v-for="book in toReadBookList">
                        <div class="col-xs-12 nopadding">
                            <book-in-read-history-list :book = "book" :refresh="refresh"></book-in-read-history-list>
                        </div>
                    </div>
                    <div v-if="gridView" class="row" v-for="splitedBook in toReadBooks">
                        <div class="col-xs-3 nopadding" v-for="book in splitedBook">
                            <book-in-read-history :book = "book" :refresh="refresh"></book-in-read-history>
                        </div>
                    </div>
                    <div v-if="toReadTotal <= per_page" style="height:15px;"></div>
                    <div v-if="toReadTotal > per_page" class="text-center">
                        <uib-pagination
                            :boundary-links="true"
                            :force-ellipses="false"
                            :boundary-link-numbers="true"
                            previous-text="<" 
                            next-text=">" 
                            first-text="<<"
                            last-text=">>"
                            v-model="toReadPagination" 
                            :items-per-page="per_page" 
                            :total-items="toReadTotal" 
                            :max-size="4" 
                            :rotate="true">
                        </uib-pagination>
                    </div>
                </div>
                <div v-if="!isShowLoading_3&&toReadTotal == 0" style="margin-left:10px;">{{toReadText}}</div>
            </div>
        </div>
    </div>
</template>

<script>
import BookInReadHistoryList from './BookInReadHistoryList.vue';
import BookInReadHistory from './BookInReadHistory.vue';
import BookService from '../../../../services/BookService.js';
import Label from '../../../../common/Label.js';
import * as CONST from '../../../../common/const.js';
export default {
    components: { BookInReadHistoryList, BookInReadHistory },
    mixins: [BookService, Label],
    mounted() {
        if (this.loginUserId) {
            this.refresh();
        }
    },
    watch: {
        loginUserId: function(newVal, oldValue){
            if(newVal) {
                this.refresh();
            }
        },
        '$route': function(){
            this.refresh();
        },
        readPagination: function(){
            var page = this.readPagination.currentPage;
            var _self = this;
            this.$router.push({ name: _self.$route.name, params: {tab: _self.$route.params.tab}, query: {nav: 'read', page: page}});
        },
        readingPagination: function(){
            var page = this.readingPagination.currentPage;
            var _self = this;
            this.$router.push({ name: _self.$route.name, params: {tab: _self.$route.params.tab}, query: {nav: 'reading', page: page}});
        },
        toReadPagination: function(){
            var page = this.toReadPagination.currentPage;
            var _self = this;
            this.$router.push({ name: _self.$route.name, params: {tab: _self.$route.params.tab}, query: {nav: 'toread', page: page}});
        }
    },
    methods: {
        setViewMode(ev, mode){
            $('.view-mode').removeClass('active');
            $(ev.target).addClass('active');
            this.gridView = mode;
        },
        getReadBooks(){
            this.getReadHistory(true, false, false, this.readPagination.currentPage);
        },
        getReadingBooks(){
            this.getReadHistory(false, true, false, this.readingPagination.currentPage);
        },
        getToReadBooks(){
            this.getReadHistory(false, false, true, this.toReadPagination.currentPage);
        },
        refresh(){
            var _self = this;
            if(this.$route.query.nav==null){
                this.$router.replace({ name: _self.$route.name, params: {tab: _self.$route.params.tab}, query: {nav: 'read', page: _self.$route.query.page}});
            }
            if(this.$route.query.page==null){
                this.$router.replace({ name: _self.$route.name, params: {tab: _self.$route.params.tab}, query: {nav: _self.$route.query.nav, page: '1'}});
            }
            this.getReadTotal();
            if (this.$route.query.nav == 'read') {
                this.readPagination = {currentPage: Number(this.$route.query.page)};
                this.getReadBooks();
            }
            if (this.$route.query.nav == 'reading') {
                this.readingPagination = {currentPage: Number(this.$route.query.page)};
                this.getReadingBooks();
            }
            if (this.$route.query.nav == 'toread') {
                this.toReadPagination = {currentPage: Number(this.$route.query.page)};
                this.getToReadBooks();
            }
        },
        getReadTotal(){
            var _self = this;
            var lcs = CONST.USER_READ_HISTORY_TOTAL;
            _self.$getItem(lcs).then(cacheData => {
                if (cacheData) {
                    if (_self.$route.query.nav == 'read') {
                        _self.readTotal = cacheData.readTotal;
                        if (_self.readTotal == 0) {
                            _self.readText = _self.label.readBookNA;
                        }
                    }
                    if (_self.$route.query.nav == 'reading') {
                        _self.readingTotal = cacheData.readingTotal;
                        if (_self.readingTotal == 0) {
                            _self.readingText = _self.label.readingBookNA;
                        }
                    }
                    if (_self.$route.query.nav == 'toread') {
                        _self.toReadTotal = cacheData.toReadTotal;
                        if (_self.toReadTotal == 0) {
                            _self.toReadText = _self.label.toReadBookNA;
                        }
                    }
                }
            });
            var config = {
                params: {
                    userId: _self.loginUserId
                }
            };
            _self.api.callGetApi(_self.api.api_get_user_reading_editions_total, config)
                .then(response => {
                    //set read total when page = 1
                    if (_self.$route.query.nav == 'read') {
                        _self.readTotal = response.data.data.readTotal;
                        if (_self.readTotal == 0) {
                            _self.readText = _self.label.readBookNA;
                        }
                    }
                    //set reading total when page = 1
                    if (_self.$route.query.nav == 'reading') {
                        _self.readingTotal = response.data.data.readingTotal;
                        if (_self.readingTotal == 0) {
                            _self.readingText = _self.label.readingBookNA;
                        }
                    }
                    //set toRead total when page = 1
                    if (_self.$route.query.nav == 'toread') {
                        _self.toReadTotal = response.data.data.toReadTotal;
                        if (_self.toReadTotal == 0) {
                            _self.toReadText = _self.label.toReadBookNA;
                        }
                    }
                    //save response data to localstorage
                    _self.$setItem(lcs, response.data.data);
                })
                .catch(this.handleErrorFunction);
        },
        getReadHistory(readFilter, readingFilter, toReadFilter, page) {
            var _self = this;
            var lcs = "";
            //get data from localforage
            if (readFilter) {
                lcs = CONST.USER_READ_HISTORY_READ + "_page_" + page;
                _self.$getItem(lcs).then(cacheData => {
                    if (!cacheData) {
                        _self.isShowLoading_1 = true;
                    }
                    _self.readBookList = cacheData;
                    _self.readBooks = _.chunk(cacheData, 4);
                })
            } else if (readingFilter) {
                lcs = CONST.USER_READ_HISTORY_READING + "_page_" + page;
                _self.$getItem(lcs).then(cacheData => {
                    if (!cacheData) {
                        _self.isShowLoading_2 = true;
                    }
                    _self.readingBookList = cacheData;
                    _self.readingBooks = _.chunk(cacheData, 4);
                })
            } else if (toReadFilter) {
                lcs = CONST.USER_READ_HISTORY_TO_READ + "_page_" + page;
                _self.$getItem(lcs).then(cacheData => {
                    if (!cacheData) {
                        _self.isShowLoading_3 = true;
                    }
                    _self.toReadBookList = cacheData;
                    _self.toReadBooks = _.chunk(cacheData, 4);
                })
            }
            //get data from Service API
            var config = {
                params: {
                    readFilter: readFilter,
                    readingFilter: readingFilter,
                    toReadFilter: toReadFilter,
                    userId: _self.loginUserId,
                    page: page,
                    per_page: _self.per_page
                }
            };
            _self.api.callGetApi(_self.api.api_get_user_reading_editions, config)
                .then(response => {
                    if (readFilter) {
                        _self.readBookList = response.data.data.resultInfo;
                        _self.readBooks = _.chunk(response.data.data.resultInfo, 4);
                        _self.isShowLoading_1 = false;
                    }
                    if (readingFilter) {
                        _self.readingBookList = response.data.data.resultInfo;
                        _self.readingBooks = _.chunk(response.data.data.resultInfo, 4);
                        _self.isShowLoading_2 = false;
                    }
                    if (toReadFilter) {
                        _self.toReadBookList = response.data.data.resultInfo;
                        _self.toReadBooks = _.chunk(response.data.data.resultInfo, 4);
                        _self.isShowLoading_3 = false;
                    }
                    //save response data to localstorage
                    _self.$setItem(lcs, response.data.data.resultInfo);
                })
                .catch(_self.handleErrorFunction);
        }
    },
    data() {
        return {
            gridView: false,
            per_page: CONST.USER_BOOK_ITEM_PERPAGE,

            //for read tab
            readPagination: {},
            readTotal: 0,
            readText: "",
            readBookList: [],
            readBooks: [],

            //for reading tab
            readingPagination: {},
            readingTotal: 0,
            readingText: "",
            readingBookList: [],
            readingBooks: [],

            //for toRead tab
            toReadPagination: {},
            toReadTotal: 0,
            toReadText: "",
            toReadBookList: [],
            toReadBooks: []
        }
    }
}
</script>

<style>
</style>



