<template>
    <div class="book-detail-content-user-comment">
        <p class="super-biger-text-form-m">{{label.txt_comment}}</p>
        <div v-if="isShowLoading">
            <img src="../../../../assets/images/loading.gif" style="width:50px; height: 50px;">
        </div>
        <div class="book-detail-content-user-comment-content" v-else>
            <list-user-create-comment :imageId="getImageUrlOnAddComment" :selfComment="selfComment" :isUpdate="isUpdate" @showRequestLoginDialog="$emit('showConfirmDialog');"
            @addComment='addComment' @cancel="isUpdate = false; isCommented = true;" v-if="false == isCommented"></list-user-create-comment>
            <hr class="line-break-color reset-padding reset-margin" v-if="false == isCommented">
            <div class="book-detail-content-user-comment-box">
                <div class="row book-detail-content-user-comment-box-item" v-if="isCommented">
                    <list-user-comment-item @editComment="editComment" :imageId="loginUser.imageId" :name="loginUser.name" :review="selfComment.review" :value="selfComment.value" :evaluationTime="selfComment.evaluationTime" :isShowEditComment="true"></list-user-comment-item>
                    <hr class="line-break-color reset-padding reset-margin">
                </div>
                <div class="row book-detail-content-user-comment-box-item" v-for="(user, index) in listUser" :key="user.userId" v-if="isDisplayUserComment(user.userId)">
                    <list-user-comment-item :imageId="user.imageId" :name="user.name" :value="user.value" :review="user.review" :evaluationTime="user.evaluationTime"></list-user-comment-item>
                    <hr class="line-break-color reset-padding reset-margin">
                </div>
                <div class="book-detail-list-user-comment-box-show-more" v-if="isShowMore">
                    <button class="btn-no-border book-detail-list-user-comment-btn-show-more" @click="showMoreComment">
                        {{label.txt_show_more_comment}}
                    </button>
                </div>
                <div style="clear: both;"></div>
            </div>
        </div>
        <div style="clear: both;"></div>
    </div>
</template>

<script>
import * as CONST from '../../../../common/const.js';
import CommonService from '../../../../services/CommonService.js';
import BookService from '../../../../services/BookService.js'; 

import ListUserCommentItem from '../listUserCommentItem/ListUserCommentItem.vue';
import ListUserCreateComment from '../listUserCreateComment/ListUserCreateComment.vue';
import Label from '../../../../common/Label.js';


export default {
    name: 'book-detail-list-user-comment',
    components: {ListUserCommentItem, ListUserCreateComment },
    mixins: [CommonService, BookService, Label],

    data() {
        return {
            listUser: [],
            isCommented: false,
            config_get_list_user_comment: {
                params: {
                    editionId: this.editionId,
                    per_page: 2,
                    page: 1
                }
            },
            config_get_self_comment: {
                params: {
                    editionId: this.editionId,
                }
            },
            selfComment: { value: 0, review: '' },
            cloneSelfComment: {},
            page: 1,
            isShowLoading: false,
            isShowMore: true,
            handleSuccess: this.setListUserComment,
            loginUser: null,
            isUpdate: false,
        }
    },
    mounted() {
        this.initData();
    },
    computed: {
        getImageUrlOnAddComment() {
            if (this.isNotNull(this.loginUser)) {
                return this.loginUser.imageId;
            }
            return '';
        },
    },
    methods: {
        initData() {
            this.isShowLoading = true;
            if (this.isNotNull(this.loginToken)) {
                this.getLoginUser();
                this.getCommentOfLoginUser();
            } else {
                this.getListUserComment();
            }
        },
        showMoreUserComment(response) {
            if (200 == response.status) {
                var responseData = response.data.data.resultInfo;
                this.listUser = this.listUser.concat(responseData);
                if (responseData.length < this.config_get_list_user_comment.params.per_page) {
                    this.isShowMore = false;
                }
                this.handleSuccess = this.setListUserComment;
            }
        },
        setListUserComment(response) {
            if (200 == response.status) {
                var responseData = response.data.data.resultInfo;
                this.listUser = responseData;
                if (responseData.length < this.config_get_list_user_comment.params.per_page) {
                    this.isShowMore = false;
                }
            }
            this.isShowLoading = false;
        },
        getListUserComment() {
            if (this.isNotNull(this.editionId)) {
                this.api.callGetApi(this.api.api_get_comment_for_book, this.config_get_list_user_comment).then(this.handleSuccess, this.handleErrorFunction);
            }
        },
        showMoreComment() {
            this.config_get_list_user_comment.params.page += 1;
            this.handleSuccess = this.showMoreUserComment;
            this.getListUserComment();
        },
        getLoginUser() {
            this.$getItem(CONST.LOGIN_USER).then(loginUser => {
                this.loginUser = this.isNotNull(loginUser) ? loginUser : null;
            });
        },
        addComment() {
            if (this.isNotNull(this.loginUserId)) {
                if (this.isNotNull(this.bookId) && this.isNotNull(this.editionId)) {
                    this.$emit("showLoadingDialog");
                    var params = {
                        evaluationId: this.selfComment.evaluationId,
                        readingId: this.readingObject.readingId,
                        bookId: this.bookId,
                        editionId: this.editionId,
                        value: this.selfComment.value,
                        review: this.selfComment.review
                    }
                    this.api.callPostApiWithAuthorization(this.api.api_selfupdate_book_evaluation, params)
                        .then(this.updateCommentSuccess, this.handleErrorUpdatecomment);
                }
            } else {
                this.$emit('showConfirmDialog');
            }
        },

        editComment() {
            // Create clone self comment
            this.cloneSelfComment.value = this.selfComment.value;
            this.cloneSelfComment.review = this.selfComment.review;
            this.isUpdate = true;
            this.isCommented = false;
        },

        handleErrorUpdatecomment(error) {
            // If update reivew
            if(this.isUpdate){
                // Restore self comment
                this.selfComment.value = this.cloneSelfComment.value;
                this.selfComment.review = this.cloneSelfComment.review;
                this.selfComment.evaluationTime = this.cloneSelfComment.evaluationTime;
                this.isCommented = true;
                this.isUpdate = false;
            }

            // Close system dialog
            this.$emit("hideLoadingDialog");
            
            // Show error dialog
            this.apiErrorDialogContent = error.response.data.message;
            this.$emit('showErrorDialog', this.apiErrorDialogContent);
        },

        updateCommentSuccess(response) {
            if (200 == response.status) {
                if (this.isUpdate) {
                    this.isUpdate = false;
                    this.isCommented = true;
                } else {
                    this.getCommentOfLoginUser();
                }
            }
            this.$emit("hideLoadingDialog");
        },
        getCommentOfLoginUser() {
            if (this.isNotNull(this.editionId)) {
                this.api.callGetApiWithAuthorization(this.api.api_selfget_book_evaluation, this.config_get_self_comment)
                    .then(this.setCommentOfUserLogin, this.handleErrorFunction);
            }
        },
        setCommentOfUserLogin(response) {
            if (200 == response.status) {
                var tempSelfComment = response.data.data.resultInfo;
                if (this.isNotNull(tempSelfComment) && this.isNotNull(tempSelfComment.evaluationId)) {
                    this.selfComment = tempSelfComment;
                    this.isCommented = true;
                }

                this.$emit("hideLoadingDialog");
            }
            this.getListUserComment();
        },
        isDisplayUserComment(userId) {
            if (this.isNotNull(this.loginUser) && userId == this.loginUser.userId) {
                return false;
            }
            return true;
        }
    },
    props: {
        bookId: {
            require: true
        },
        editionId: {
            require: true
        },
        readingObject: {
            required: true
        }
    }
}

</script>

<style lang="scss">
@import '../../../../scss/common_styles.scss';
@import '../../../../scss/text_styles.scss';
@import 'ListUserCommentInDetailPage.scss';
</style>




